.form__container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: default;
  pointer-events: none;
  transition-duration: 0.5s;
  transition-property: background;
}

.backgroundclear {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  pointer-events: fill;
  z-index: 4;

  transition: 0.25s;
}

.backgroundunclear {
  background: rgba(0, 0, 0, 0);
  pointer-events: none;
}

.design__form {
  position: absolute;
  bottom: 4%;
  right: 4%;
  width: 7.7vh;
  height: 7.7vh;
  border-radius: 50%;
  border: 0.5vh solid white;
  background: rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 0.25s;
  cursor: default;
  z-index: 100;
  pointer-events: fill;
}

.form__buttons {
  position: absolute;
  pointer-events: fill;
  bottom: 4%;
  right: 4%;
  width: 7.7vh;
  height: 7.7vh;
  transition-duration: 1;
  cursor: pointer;
}
.button__create {
  transform: rotateZ(-45deg);
  fill: white;
  width: 70%;
  height: 70%;
  position: absolute;
}
.form__buttons_hover {
  transform: scale(1, 1) rotateZ(45deg) !important;
  pointer-events: fill;
}
.create__post {
  position: absolute;
  border: 0.25vh solid white;
  top: -1.75vh;
  left: -5vh;
  width: 40%;
  height: 40%;
  transform: scale(0, 0) rotateZ(45deg);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1110;

  transition: 0.25s;
  transition-property: transform;
}
.unknown1 {
  position: absolute;
  border: 0.25vh solid white;

  top: 1.75vh;
  left: -8.25vh;
  width: 40%;
  height: 40%;
  transform: scale(0, 0) rotateZ(-45deg);
  transition: 0.25s;
  transition-delay: 0.1s;
  transition-property: transform;
}
.unknown2 {
  position: absolute;
  border: 0.25vh solid white;
  left: -5vh;
  bottom: -0.75vh;
  width: 40%;
  height: 40%;
  transform: scale(0, 0) rotateZ(-135deg);
  transition: 0.25s;
  transition-delay: 0.2s;
  transition-property: transform;
}
.form_hover {
  border-top-left-radius: 0%;
  border-bottom-right-radius: 0%;
  transform: rotateZ(135deg);
}
.form_designHover {
  transform: rotateZ(45deg);
  /* box-shadow: 0px 0px 15px 0px white; */
}
.form_designHover2 {
  transform: rotateZ(45deg);
  width: 85% !important;
  height: 85% !important;
}
.form_designQuestion {
  fill: rgba(255, 255, 255, 1) !important;

  transform: rotateZ(135deg) !important;
}

.design1__square {
  position: relative;
  top: 0;
  left: 0;
  width: 65%;
  height: 65%;
  /* background: red; */
  border: 0.5vh solid rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 1s;
  pointer-events: none;
  overflow: hidden;
}
.period {
  width: 35%;
  height: 35%;
  background: white;
  border-radius: 100%;
  transition-duration: 0.25s;
  transition-property: transform;
}
.period__remove {
  transform: scale(0, 0);
}

.design2__square {
  position: relative;
  top: 0;
  left: 0;
  width: 55%;
  height: 55%;
  /* background: red; */
  border: 0.4vh solid rgba(255, 255, 255, 0.5);
  transition-duration: 0.25s;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.question {
  position: absolute;
  width: 100%;
  height: 100%;
  fill: rgba(255, 255, 255, 0);
  transform: none;
  transition-duration: 0.5s;
  transform: rotateZ(0deg);
}

.showing {
  transform: scale(1, 1) !important;
}
